.content-main-container {
    width: 100%;
    height: 100vh;
    display: flex;
}

.content-left-container {
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.content-right-container {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 8%;

}

.content-left-container-overall:hover {
    padding: 40px;
    background-color: #D84F72;
    color: #fff;
    cursor: pointer;

}

.content-left-container-overall {
    width: 50%;
    height: auto;
    padding: 40px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    transition: all ease-in-out 0.4s;
    color: #D84F72;
}

.content-card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 60%;
    margin-right: 30%;
    margin: auto;
    text-align: center;
    font-family: arial;
    color: #D84F72;
    transition: all ease-in-out 0.2s;
}

.content-card:hover {
    transform: scale(1.1, 1.1);
    -webkit-box-shadow: -1px 2px 29px 0px rgba(216, 79, 114, 1);
    -moz-box-shadow: -1px 2px 29px 0px rgba(216, 79, 114, 1);
    box-shadow: -1px 2px 29px 0px rgba(216, 79, 114, 1);
    cursor: pointer;
}

.content-title {
    color: #D84F72;
    font-size: 25px;

}

.content-card-button {
    border: none;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #D84F72;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
}


.content-image {
    border-radius: 50%;
}

.content-left-container-header {
    font-size: 30px;
    padding-bottom: 20px;
    font-weight: 800;
}

.content-left-container-content {
    font-size: 20px;

}

